.pizza-of-the-month-main-container {
    position: relative;
    margin: 50px auto 20px;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background: #793d3d;
    color: white;
  }
  
  .pizza-of-the-month-wrapper {
    position: relative;
    width: auto;
    height: auto;
    text-align: center;
    margin: 0;
    top: 0;
    padding: 25px;
    background: #793d3d;
  }
  
  .heart-icon {
    width: 450px;
    margin-top: 30px;
    animation: animation .5s ease infinite;
  }
  
  @keyframes animation {
    from { transform: scale(1); }
    50% { transform: scale(1.05); }
    to { transform: scale(1); }
  }
  
  .pizza-bestseller-img, .pizza-of-the-month-description {
    display: inline-block;
    vertical-align: top;
  } 
  
  .pizza-bestseller-img {
    position: relative;
    width: 660px;
    height: 510px;
    border: 10px solid white;
    border-radius: 270px;
    transition: transform 250ms;
    transition-timing-function: ease-in-out;
  }
  
  .pizza-of-the-month-description {
    position: relative;
    max-width: 680px;
    width: auto;
    height: auto;
  }
  
  .pizza-of-the-month-title {
    text-align: center;
  }
  
  .pizza-of-the-month-content {
    text-align: center;
  }
  
  .pizza-of-the-month-price {
    color: #ffa36e;
    transition-property: color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
  }

  @media screen and (max-width: 768px) {
    .pizza-of-the-month-main-container {
      margin: 30px auto 10px;
    }

    .pizza-bestseller-img {
      width: 440px;
      height: 340px;
      border: 6px solid white;
    }

    .heart-icon {
      width: 300px;
      margin-top: 10px;
    }
  }

  @media screen and (max-width: 500px) {
    .pizza-of-the-month-main-container {
      margin-bottom: 0;
    }

    .pizza-bestseller-img {
      width: 295px;
      height: 228px;
      border: 4px solid white;
    }

    .heart-icon {
      width: 200px;
      margin-top: 0;
    }

    .pizza-of-the-month-title {
      font-size: 16px;
    }

    .pizza-of-the-month-content, .pizza-of-the-month-price-description {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 350px) {
    .pizza-bestseller-img {
      width: 200px;
      height: 155px;
      border: 2px solid white;
    }

    .heart-icon {
      width: 120px;
    }
  }

  @media screen and (max-width: 235px) {
    .pizza-bestseller-img {
      width: 130px;
      height: 100px;
      border: 2px solid white;
    }

    .heart-icon {
      width: 90px;
    }

    .pizza-of-the-month-title {
      font-size: 14px;
    }

    .pizza-of-the-month-content, .pizza-of-the-month-price-description {
      font-size: 12px;
    }
  }