.shopping-cart-wrapper {
  position: fixed;
  border-radius: 10px;
  box-shadow: 8px 10px 12px #4a4545;
  margin: 0;
  width: 80%;
  max-width: 800px;
  height: auto;
  background-color: white;
  top: 50%;
  left: 50%;
  font-family: 'Architects Daughter', cursive;
  font-size: 20px;
  transform: translate(-50%, -50%);
  font-weight: bold;
  color: black;
  min-width: 200px;
}

.empty-shopping-cart-text {
  position: relative;
  margin: 16px 0;
  padding: 0;
  width: 100%;
  height: auto;
  text-align: center;
}

.empty-cart-icon {
  width: 60px;
  height: 60px;
  transition-property: transform;
  transition-duration: 1250ms;
  transition-timing-function: cubic-bezier(0.68, -0.66, 0.265, 1.33);
  transform: rotate(-360deg);
}

.empty-cart-icon:hover {
  transform: rotate(360deg);
}

.empty-cart-icon-animation {
  animation:show-empty-cart-icon 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s 1, rotate-empty-cart-icon 1500ms cubic-bezier(0.68, -0.66, 0.265, 1.33) 600ms 1;
}

@keyframes show-empty-cart-icon {
  from {transform: translateX(-250%)}
  30% {transform: translateX(100%)}
  to {transform: translateX(0)}
}

@keyframes rotate-empty-cart-icon {
  from {transform: rotate(0deg)}
  50% {transform: rotate(360deg);}
  to {transform: rotate(0deg);}
}

.shopping-cart-list {
  position: relative;
  margin: 2px auto 0;
  top: 0;
  left: 0;
  width: calc(100% - 16px);
  height: auto;
  max-height: 500px;
  list-style-type: none;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 5px;
}

.shopping-cart-btns-wrapper {
  position: relative;
  left: 0;
  width: 100%;
  height: auto;
  margin: 5px 0;
  text-align: center;
}

.shopping-cart-btn {
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  color: #ffffff;
  background: orange;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 1px 6px;
  transition: all .25s;
}

.shopping-cart-btn:hover {
  background: #d69315;
}

.shopping-cart-btn-icon {
  width: 16px;
  height: 16px;
  top: 2px;
  position: relative;
  margin-right: 5px;
}

.total-items-price {
  margin: 10px auto 5px;
  padding: 0;
  width: 100%;
  text-align: center;
  font-size: 16px;
  color: #967e52;
}

@media screen and (max-width: 640px) {  
  .shopping-cart-wrapper {
    font-size: 16px;
    box-shadow: 6.4px 8px 9.6px #4a4545;
  }

  .empty-cart-icon {
    width: 48px;
    height: 48px;
  }

  .shopping-cart-list {
    max-height: 400px;
  }

  .total-items-price {
    font-size: 14px;
    margin-bottom: 1px;
    margin-top: 7px;
  }

  .shopping-cart-btn-icon {
    width: 14px;
    height: 14px;
  }

  .shopping-cart-btn {
    font-size: 14px;
  }
}

@media screen and (max-width: 510px) {  
  .shopping-cart-wrapper {
    font-size: 13px;
    box-shadow: 5.2px 6.5px 7.8px #4a4545;
  }

  .empty-cart-icon {
    width: 39px;
    height: 39px;
  }

  .shopping-cart-list {
    max-height: 320px;
  }

  .total-items-price {
    font-size: 12px;
  }

  .shopping-cart-btn-icon {
    width: 12px;
    height: 12px;
    top: 1.4px;
  }

  .shopping-cart-btn {
    font-size: 12px;
  }
}

@media screen and (max-width: 420px) {  
  .shopping-cart-wrapper {
    font-size: 11px;
    box-shadow: 4.4px 5.5px 6.6px #4a4545;
  }

  .empty-cart-icon {
    width: 33px;
    height: 33px;
  }

  .shopping-cart-list {
    max-height: 240px;
  }

  .total-items-price {
    font-size: 10px;
  }

  .shopping-cart-btn-icon {
    width: 10px;
    height: 10px;
    top: 1px;
  }

  .shopping-cart-btn {
    font-size: 10px;
  }
}

@media screen and (max-width: 360px) {
  .total-items-price {
    margin-top: 0;
  }

  .shopping-cart-wrapper {
    font-size: 8px;
    box-shadow: 3.2px 4px 4.8px #4a4545;
  }

  .empty-cart-icon {
    width: 24px;
    height: 24px;
  }

  .discount {
    width: 10px;
  }
}