.main-content-top-wrapper {
    position: relative;
    top: 60px;
    left: 0;
    width: 100%;
    height: 870px;
    font-family: 'Prompt', sans-serif;
}

.end-description-text {
    position: absolute;
    margin: 0;
    padding: 0;
    top: calc(100% - 60px);
    left: 0;
    width: 100%;
    height: 40px;
    color: white;
    text-align: center;
    font-size: 24px;
}

@media screen and (max-width: 768px) {
    .end-description-text {
        font-size: 20px;
    }

    .main-content-top-wrapper {
        height: 760px;
    }
}

@media screen and (max-width: 550px) {
    .main-content-top-wrapper {
        height: 650px;
    }
}