.main-title-container {
    position: relative;
    max-width: 950px;
    width: auto;
    height: auto;
    margin: 0 auto;
    padding: 0 10px;
    top: calc(50% - 180px);
}
  
  .main-title {
    font-size: 55px;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}
  
  .main-description {
    font-size: 24px;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}

@media screen and (max-width: 835px) {
  .main-title {
    font-size: 45px;
  }

  .main-description {
    font-size: 20px;
  }
}

@media screen and (max-width: 620px) {
  .main-title {
    font-size: 40px;
  }

  .main-description {
    font-size: 18px;
  }
}

@media screen and (max-width: 500px) {
  .main-title {
    font-size: 36px;
  }
}

@media screen and (max-width: 440px) {
  .main-title {
    font-size: 32px;
  }

  .main-description {
    font-size: 16px;
  }
}

@media screen and (max-width: 315px) {
  .main-title {
    font-size: 24px;
  }

  .main-description {
    font-size: 14px;
  }
}