nav {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  width: auto;
  padding-left: 5px;
  height: 100%;
  z-index: 999;
  vertical-align: top;
}

.navbar-item {
  position: relative;
  display: inline-block;
  list-style: none;
  font-size: 18px;
  top: calc(50% - 12px);
  margin: 0 10px;
  font-weight: bold;
  font-family: 'Prompt', sans-serif;
}

.navbar-item-link {
  text-decoration: none;
  color: aliceblue;
  cursor: pointer;
  transition: .25s;
}

.navbar-item-link:hover {
  color: #c9dbeb;
}

.navbar-mob-container {
 display: none;
 margin: 0;
 padding: 1px 5px;
 width: auto;
 height: auto; 
}

.first, .second, .third {
  width: 100%;
  height: 2px;
  border-radius: 20px;
  background-color: aliceblue;
  margin: 6px 0 0 0;
  padding: 0;
  display: block;
  position: relative;
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: ease-in;
}

.navbar-container {
  width: 20px;
  height: 20px;
  position: relative;
  top: 14px;
  left: 12px;
  cursor: pointer;
}

.mob-navigation-container {
  display: none;
  top: 31px;
  background-color: #171021;
  width: 65px;
  height: 65px;
  padding: 6px;
  border-top: 1px solid aliceblue;
  text-align: left;
  transition-property: width;
  transition-duration: 250ms;
  transition-timing-function: ease;
}

.mob-navbar-item {
  display: block;
  top: 0;
  font-size: 16px;
}


@media screen and (max-width: 768px) {
  .mob-navigation-container {
    display: block;
  }

  .navigation-container {
    display: none;
  }

  .navbar-mob-container {
    display: block;
  }
}

@media screen and (max-width: 220px) {
  .navbar-container {
    width: 15px;
    left: 1px;
  }
}

@media screen and (max-width: 265px) {
  .navbar-container {
    width: 15px;
    left: 1px;
  }

  .mob-navigation-container {
    width: calc(100vw - 30px);
    text-align: center;
  }
}

@media screen and (max-width: 185px) {
  .navbar-container {
    width: 10px;
    left: 0;
  }
}