.middle-wrapper-title {
    position: relative;
    width: 100%;
    font-size: 28px;
    text-align: center;
    margin: 0;
    top: 25px;
    color: #0c0c0c;
    font-weight: 600;
}
  
  .why-us-list {
    position: relative;
    margin: 30px auto 0;
    width: calc(100% - 40px);
    padding: 0 20px;
    max-width: 1660px;
    height: auto;
    text-align: center;
    list-style-type: none;
}
  
  .why-us-list-item-container {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    margin: 20px 25px 0;
    vertical-align: top;
    max-width: 425px;
    transition: .5s;
    transform: scale(1);
}
  
  .why-us-image {
    width: 425px;
    height: 255px;
    border-radius: 5px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
  
  .why-us-list-item {
    position: relative;
    margin: 7px 0;
    font-size: 16px;
}

@media screen and (max-width: 550px) {
  .why-us-image {
    width: 283px;
    height: 170px;
  }

  .why-us-list-item {
    font-size: 14px;
  }

  .middle-wrapper-title {
    font-size: 24px;
  }
}

@media screen and (max-width: 400px) {
  .why-us-image {
    width: 200px;
    height: 120px;
  }

  .why-us-list-item {
    font-size: 12px;
    margin: 1px 0;
  }

  .middle-wrapper-title {
    font-size: 20px;
  }
}

@media screen and (max-width: 300px) {
  .why-us-image {
    width: 150px;
    height: 90px;
  }
}