.shopping-cart-icon-wrapper {
    position: absolute;
    text-align: center;
    left: calc(100% - 90px);
    width: 80px;
    white-space: nowrap;
    top: 12px;
    height: calc(50% - 5px);
    color: #b4efcb;
    border-radius: 7px;
    background: #5d8e50;
    padding: 6px 1px;
    cursor: pointer;
    transition-property: all;
    transition-duration: 400ms;
    transition-timing-function:cubic-bezier(0.23, 1, 0.320, 1);
  }

  .added-item {
    animation: scale 1s ease-in-out infinite;
  }

  @keyframes scale {
    from { transform: scale(1); }
    25% { transform: scale(1.05); }
    50% { transform: scale(1); }
    75% { transform: scale(1.05); }
    to { transform: scale(1); }
  }
  
  .shopping-cart-icon-wrapper:hover {
    background: #4e7942;
  }
  
  .shopping-cart-icon {
    width: 24px;
    cursor: pointer;
  }

  @media screen and (max-width: 768px) {
    .shopping-cart-icon-wrapper {
      width: 65px;
      left: calc(100% - 75px);
    }
  }

  @media screen and (max-width: 440px) {
    .shopping-cart-icon-wrapper {
      width: 50px;
      left: calc(100% - 60px);
      height: calc(40% - 5px);
      top: 16px;
    }

    .shopping-cart-icon {
      width: 20px;
    }
  }

  @media screen and (max-width: 265px) {
    .shopping-cart-icon-wrapper {
      width: 26px;
      left: calc(100% - 40px);
      height: calc(35% - 5px);
      top: 20px;
    }

    .shopping-cart-icon-wrapper {
      padding: 4px 0;
      left: calc(100% - 30px);
      height: calc(35% - 3px);
    }

    .shopping-cart-icon {
      width: 14px;
    }
  }