footer {
    position: relative;
    padding: 10px 0;
    top: 30px;
    left: 0;
    width: 100%;
    height: auto;
    background-color: #21191a;
    color: white;
  }
  
  .footer-title {
    position: relative;
    font-size: 32px;
    margin: 0;
    text-align: center;
    font-family: 'Prompt', sans-serif;
  }
  
  .follow-us-list {
    position: relative;
    margin: 30px auto 40px;
    padding: 0;
    text-align: center;
    list-style-type: none;
    width: auto;
    height: auto;
  }
  
  .follow-us-list-item {
    display: inline-block;
    width: 60px;
    height: 60px;
    margin: 5px 30px;
    border-radius: 50%;
    border: 5px solid #3e3636;
    cursor: pointer;
    transition-property: transform, border-color;
    transition-duration: 600ms, 400ms;
    transition-timing-function: cubic-bezier(0.23, 1, 0.320, 1), cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  
  .follow-us-list-item:hover {
    border: 5px solid #5c5656;
  }
  
  .follow-us-icon {
    width: 100%;
    height: 100%;
  }
  
  .work-schedule {
    position: relative;
    margin: 40px auto 0;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 16px;
    font-family: 'Architects Daughter', cursive;
  }
  
  .copyright {
    position: relative;
    margin: 40px auto 5px;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 12px;
    text-align: center;
    font-family: 'Prompt', sans-serif;
  }

  @media screen and (max-width: 768px) {
    .footer-title {
      font-size: 28px;
    }

    .follow-us-list-item {
      width: 50px;
      height: 50px;
      margin: 5px 20px;
    }

    .follow-us-list {
      margin-bottom: 15px;
    }

    .work-schedule, .copyright {
      margin-top: 30px;
    }
  }

  @media screen and (max-width: 440px) {
    .footer-title {
      font-size: 24px;
    }

    .follow-us-list-item {
      border: 4px solid #3e3636;
    }
    
    .follow-us-list-item:hover {
      border: 4px solid #5c5656;
    }
    
    .work-schedule {
      font-size: 14px;
    }

    .copyright {
      font-size: 10px;
    }
  }