.where-to-find-us-wrapper {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background-color: white;
    padding: 20px 0;
  }
  
  .where-to-find-us-title {
    position: relative;
    margin: 0;
    width: 100%;
    text-align: center;
    font-family: 'Architects Daughter', cursive;
  }
  
  .where-to-find-us-list {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    list-style-type: none;
    font-family: 'Prompt', sans-serif;
  }
  
  .where-to-find-us-list-item {
    position: relative;
    margin: 10px auto 0;
    font-size: 16px;
  }
  
  .where-to-find-us-list-item-wrapper {
    position: relative;
    display: block;
    width: auto;
    height: auto;
    text-align: center;
    margin: 15px auto;
    padding: 10px;
  }
  
  .where-to-find-us-icon {
    width: 50px;
    height: 50px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: ease;
    transform: scale(1);
  }

  .where-to-find-us-icon:hover {
    transform: scale(1.25);
  }

  @media screen and (max-width: 500px) {
    .where-to-find-us-title {
      font-size: 20px;
    }

    .where-to-find-us-list-item {
      font-size: 14px;
    }

    .where-to-find-us-icon {
      width: 40px;
      height: 40px;
    }
  }