.menu-container {
    position: relative;
    top: 0;
    left: 0;
    width: calc(100% - 40px);
    padding: 10px 20px;
    height: auto;
}
  
  .menu-toggle-list {
    margin: 0 auto;
    padding: 0;
}
  
  .menu-toggle-list-item {
    position: relative;
    display: inline-block;
    margin: 5px 30px;
    padding: 0;
    font-size: 20px;
    height: 34px;
    font-weight: 800;
    cursor: pointer;
}
  
  .menu-list-item-selected {
    border-bottom: 1px solid #ccacac;
}
  
  .menu-list {
    position: relative;
    margin: 25px auto 20px;
    width: 100%;
    max-width: 1550px;
    height: auto;
    text-align: center;
    list-style-type: none;  
}