.shopping-cart-list-item-wrapper {
    position: relative;
    margin: 10px 0;
    display: block;
    width: calc(100% - 6px);
    height: 100px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.68, -1.66, 0.265, 1.33);
  }

  .shopping-cart-border {
    position: absolute;
    margin: 0;
    border: 3px dashed wheat;
    border-radius: 55px;
    display: block;
    width: calc(100% - 6px);
    height: calc(100% - 6px);
    z-index: 9;
  }

  .shopping-cart-delete-btn {
    position: absolute;
    margin: 0;
    left: calc(100% - 46px);
    top: calc(50% - 16px);
    border-radius: 5px;
    border: 2px dashed #e6cfa7;
    background-color: transparent;
    padding: 2px 5px;
    cursor: pointer;
    transition-property: border-color;
    transition-duration: 1s;
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
    z-index: 99;
    transform: none;
  }

  .shopping-cart-delete-btn:hover {
    border-color: #d1430f;
  }

  .delete-item-icon {
    width: 20px;
    position: relative;
    top: 1px;
  }

  .shopping-cart-list-item-img-over-container {
    position: absolute;
    display: inline-block;
    width: 90px;
    height: 90px;
    top: 0;
    left: 0;
    z-index: 999;
  }

  .added-item-img {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 90px;
    height: 90px;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  }

  .shopping-cart-list-item {
    position: relative;
    display: inline-block;
    width: auto;
    top: 1.5px;
    height: auto;
    text-align: left;
    z-index: 99;
  }

  .shopping-cart-item-count {
    margin-left: 15px;
  }

  .shopping-cart-total-price {
    display: block;
    width: 100%;
    text-align: center;
    top: calc(100% - 60px);
    font-size: 16px;
  }

  .total-price-content {
    font-size: 18px;
    color: #967e52;
  }

  @media screen and (max-width: 640px) {
    .shopping-cart-list-item-wrapper {
      height: 75px;
    }
    
    .shopping-cart-delete-btn {
      left: calc(100% - 42px);
      top: calc(50% - 14px);
    }

    .delete-item-icon {
      width: 16px;
    }

    .added-item-img {
      width: 65px;
      height: 65px;
      top: 5px;
    }

    .shopping-cart-item-count {
      margin-left: 10px;
    }

    .shopping-cart-total-price {
      font-size: 14px;
      top: calc(100% - 51px);
    }

    .total-price-content {
      font-size: 16px;
    }
  }

  @media screen and (max-width: 510px) {
    .shopping-cart-list-item-wrapper {
      height: 60px;
    }
    
    .shopping-cart-delete-btn {
      left: calc(100% - 39px);
      top: calc(50% - 13px);
    }

    .delete-item-icon {
      width: 14px;
    }

    .added-item-img {
      width: 50px;
      height: 50px;
    }

    .shopping-cart-total-price {
      font-size: 12px;
      top: calc(100% - 42px);
    }

    .total-price-content {
      font-size: 13px;
    }
  }

  @media screen and (max-width: 420px) {
    .shopping-cart-list-item-wrapper {
      height: 50px;
    }
    
    .shopping-cart-delete-btn {
      left: calc(100% - 37px);
      top: calc(50% - 12px);
    }

    .delete-item-icon {
      width: 12px;
    }

    .added-item-img {
      width: 40px;
      height: 40px;
    }

    .shopping-cart-item-count {
      margin-left: 7px;
    }

    .shopping-cart-total-price {
      font-size: 10px;
      top: calc(100% - 36px);
    }

    .total-price-content {
      font-size: 11px;
    }
  
  }

  @media screen and (max-width: 360px) {
    .shopping-cart-list-item-wrapper {
      margin-bottom: 0;
      height: 40px;
    }

    .shopping-cart-delete-btn {
      left: calc(100% - 34px);
      top: calc(50% - 11px);
      padding: 1px 4px;
    }

    .delete-item-icon {
      width: 12px;
    }

    .added-item-img {
      width: 30px;
      height: 30px;
    }

    .shopping-cart-total-price {
      font-size: 8px;
      top: calc(100% - 29px);
    }

    .total-price-content {
      font-size: 9px;
    }
  }