.first-menu-look-list {
  position: absolute;
  margin: 0;
  top: calc(100% - 260px);
  padding: 15px 0;
  left: 0;
  width: 100%;
  height: auto;
  color: white;
  font-weight: 600;
  list-style-type: none;
  text-align: center;
  border-top: 1px solid #f5dfdf;
  border-bottom: 1px solid #f5dfdf;
}

.first-menu-look-list-item-wrapper {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  margin: 10px 40px;
  transition-property: transform;
  transition-duration: 300ms;
  transition-timing-function:cubic-bezier(0.47, 0, 0.745, 0.715);
}

.first-menu-look-list-item {
  position: relative;
  width: auto;
  height: auto;
  display: inline-block;
  font-size: 28px;
}

.first-menu-look-list-item-icon {
  display: inline-block;
  width: 28px;
  height: 28px;
  vertical-align: sub;
  margin-left: 5px;
}

@media screen and (max-width: 875px) {
  .first-menu-look-list-item {
    font-size: 24px;
  }

  .first-menu-look-list-item-icon {
    width: 24px;
    height: 24px;
  }
}

@media screen and (max-width: 570px) {
  .first-menu-look-list-item-wrapper {
    margin: 5px 25px;
  }

  .first-menu-look-list-item {
    font-size: 24px;
  }

  .first-menu-look-list {
    top: calc(100% - 270px);
  }

  .first-menu-look-list-item-icon {
    width: 24px;
    height: 24px;
  }
}

@media screen and (max-width: 440px) {
  .first-menu-look-list-item {
    font-size: 20px;
  }

  .first-menu-look-list-item-icon {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 260px) {
  .first-menu-look-list-item {
    font-size: 16px;
  }

  .first-menu-look-list-item-icon {
    width: 16px;
    height: 16px;
  }
}

