.empty-cart-icon {
  width: 60px;
  height: 60px;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 1250ms;
  transition-timing-function: cubic-bezier(0.68, -0.66, 0.265, 1.33);
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
}

.empty-cart-icon:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.empty-cart-icon-animation {
  -webkit-animation:show-empty-cart-icon 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s 1, rotate-empty-cart-icon 1500ms cubic-bezier(0.68, -0.66, 0.265, 1.33) 600ms 1;
          animation:show-empty-cart-icon 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s 1, rotate-empty-cart-icon 1500ms cubic-bezier(0.68, -0.66, 0.265, 1.33) 600ms 1;
}

@-webkit-keyframes show-empty-cart-icon {
  from {-webkit-transform: translateX(-250%);transform: translateX(-250%)}
  30% {-webkit-transform: translateX(100%);transform: translateX(100%)}
  to {-webkit-transform: translateX(0);transform: translateX(0)}
}

@keyframes show-empty-cart-icon {
  from {-webkit-transform: translateX(-250%);transform: translateX(-250%)}
  30% {-webkit-transform: translateX(100%);transform: translateX(100%)}
  to {-webkit-transform: translateX(0);transform: translateX(0)}
}

@-webkit-keyframes rotate-empty-cart-icon {
  from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  50% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  to {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

@keyframes rotate-empty-cart-icon {
  from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  50% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  to {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

