.contact-us-wrapper {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
  }

  .contact-us-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .contact-us-form {
    position: relative;
    width: 60%;
    padding: 10px 0;
    border-radius: 50px;
    height: auto;
    margin: 0 30px auto auto;
    top: 5%;
    background: transparent;
    color: white;
  }
  
  .contact-us-main-title {
    font-size: 32px;
    margin: 0;
    font-family: 'Architects Daughter', cursive;
    text-align: center;
  }
  
  .contact-us-title {
    margin: 15px auto;
    font-family: 'Architects Daughter', cursive;
    text-align: center;
    font-size: 20px;
  }
  
  .contact-us-input {
    margin: 20px auto 0;
    position: relative;
    display: block;
    width: 75%;
    border: none;
    border-bottom: 1px solid #e4dcdc;
    background: transparent;
    padding: 5px;
    text-align: center;
    color: white;
    font-family: 'Prompt', sans-serif;
    font-size: 14px;
  }
  
  .contact-us-textarea {
    resize: none;
    height: 100px;
  }
  
  .contact-us-textarea::-webkit-scrollbar {
    width: 10px;
    background-color: #f9f9fd;
  }
  
  .contact-us-textarea::-webkit-scrollbar-thumb {
    background-color: #9c9c9c;
  }
  
  .contact-us-textarea::-webkit-scrollbar-track {
    background-color: #f9f9fd;
  }
  
  .contact-us-input::placeholder {
    color: white;
  }
  
  .contact-us-input:focus {
    outline: none;
  }
  
  .contact-us-submit-btn {
    display: block;
    transition: .25s;
    position: relative;
    width: auto;
    margin: 15px auto 5px;
    height: auto;
    padding: 10px 15px;
    font-size: 24px;
    color: white;
    background: #636363;
    border-radius: 15px;
    border: 2px solid #bb8c8c;
    cursor: pointer;
    font-family: 'Architects Daughter', cursive;
  }
  
  .contact-us-submit-btn:hover {
    background-color: #565656;
  }

  @media screen and (max-width: 500px) {
    .contact-us-main-title {
      font-size: 24px;
    }

    .contact-us-title {
      font-size: 16px;
    }

    .contact-us-submit-btn {
      font-size: 20px;
    }
  }