.logo-wrapper {
    position: relative;
    display: inline-block;
    white-space: nowrap;
    font-size: 32px;
    color: white;
    text-align: center;
    width: 100%;
    font-family: 'Pacifico', cursive;
}
  
  .logo {
    width: 24px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: ease-in-out;
}

.logo:hover {
  transform: rotate(360deg);
}

@media screen and (max-width: 768px) {
  .logo-wrapper {
    font-size: 28px;
    top: 5px;
  }

  .logo {
    width: 20px;
  }
}

@media screen and (max-width: 440px) {
  .logo-wrapper {
    font-size: 24px;
    top: 8px;
  }

  .logo {
    width: 16px;
  }
}

@media screen and (max-width: 220px) {
  .logo-wrapper {
    font-size: 20px;
    top: 12px;
  }

  .logo {
    width: 14px;
  }
}

@media screen and (max-width: 220px) {
  .logo-wrapper {
    font-size: 16px;
    top: 16px;
  }

  .logo {
    width: 12px;
  }
}