  .menu-list-item-container {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    margin: 10px 35px;
    vertical-align: top;
    max-width: 250px;  
}
  
  .menu-list-image {
    position: relative;
    width: 250px;
    height: 250px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}
  
  .menu-list-item-name {
    text-align: center;
    font-size: 18px;
    margin-top: 5px;
    font-weight: 800;
}
  
  .counter-wrapper {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    user-select: none;
}
  
  .counter-item {
    display: inline-block;
    margin: 0 5px;
    font-size: 20px;
    font-weight: bold;
    color: #efa436;
    vertical-align: sub;
}
  
  .left-arrow, .right-arrow {
    cursor: pointer;
    transition: .2s;
}

.left-arrow:hover, .right-arrow:hover {
  color: #bd9f73;
}
  
  .add-to-shopping-cart-btn {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    text-align: center;
    background: transparent;
    cursor: pointer;
    color: #efa436;
    border: 2px solid #efa436;
    border-radius: 5px;
    transition: .2s;
}

.add-to-shopping-cart-btn:hover {
  border-color: #bd9f73;
  color: #bd9f73;
}
  
  .pizza-size, .menu-list-item-price {
    display: inline-block;
    margin: 0 6px;
    font-weight: 800;
}
  
  .menu-list-item-description {
    margin: 0;
}
  
  .menu-list-item-size-container {
    font-size: 14px;
    font-family: 'Indie Flower', cursive;
    margin: 5px auto 10px;
}
  
  .pizza-size {
    display: inline-block;
    border-radius: 15px;
    padding: 6px;
    user-select: none;
    cursor: pointer;
    border: none;
    transition: background-color;
    transition-duration: 600ms;
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

  .pizza-size, .pizza-size:disabled {
    background: #dadada;
    color: #377ab5;
}

.pizza-size:hover {
  background-color: #c7c6c6;
}
  
  .menu-list-item-price {
    font-family: 'Architects Daughter', cursive;
    font-size: 24px;
    color: #efa436;
}

.selected, .selected:disabled, .selected:hover {
    background: #a5b5c3;
    color: #377ab5;
}

.hide {
    animation: hide .25s ease-out;
}
  
.show {
    animation: show .25s ease-out;
}

@keyframes hide {
    from {background: #a5b5c3;}
    to {background: #dadada;}
}

@keyframes show {
    from {background: #dadada;}
    to {background: #a5b5c3;}
}

@media screen and (max-width: 768px) {
  .menu-list-item-container {
    margin: 10px 20px;
  }

  .menu-list-image {
    width: 200px;
    height: 200px;
  }
}

@media screen and (max-width: 589px) {
  .menu-list {
    margin: 15px auto 5px;
  }
}

@media screen and (max-width: 470px) {
  .menu-list-item-price {
    font-size: 22px;
  }
}

@media screen and (max-width: 250px) {
  .menu-list-item-container {
    margin: 10px 10px;
  }

  .menu-list-image {
    width: 140px;
    height: 140px;
  }
}